<template>
  <div v-if="product">
    <div class="img-wrap">
      <img :src="product.imageUrl" />
    </div>
    <div class="product-details">
      <h1>{{ product.name }}</h1>
      <h3 class="price">{{ product.price }}</h3>
      <button
        @click="addToCart"
        class="add-to-cart"
        v-if="user && !isItemInCart">
        Add to Cart
      </button>
      <button class="grey-button" v-if="user && isItemInCart">
        Already in Cart
      </button>
      <button class="sign-in" @click="signIn" v-if="!user">
        Sign in to add to cart
      </button>
    </div>
  </div>
  <div v-else>
    <NotFoundPage />
  </div>
</template>

<script>
import axios from 'axios';
import {
  getAuth,
  sendSignInLinkToEmail,
  isSignInWithEmailLink,
  signInWithEmailLink,
} from 'firebase/auth';
import NotFoundPage from '@/pages/NotFoundPage.vue';
export default {
  name: 'ProductsDetailPage',
  props: ['user'],
  components: { NotFoundPage },
  data() {
    return {
      product: {},
      cartItems: [],
    };
  },
  async created() {
    const auth = getAuth();
    if (isSignInWithEmailLink(auth, window.location.href)) {
      const email = window.localStorage.getItem('emailForSignIn');
      await signInWithEmailLink(auth, email, window.location.href);
      alert('Successfully signed in!');
      window.localStorage.removeItem('emailForSignIn');
    }
    const response = await axios.get(
      `/api/products/${this.$route.params.productId}`,
    );
    const product = response.data;
    this.product = product;

    if (this.user) {
      const cartResponse = await axios.get(`/api/users/${this.user.uid}/cart`);
      const cartItems = cartResponse.data;
      this.cartItems = cartItems;
    }
  },
  computed: {
    isItemInCart() {
      return this.cartItems.some(
        item => item.id === this.$route.params.productId,
      );
    },
  },
  watch: {
    async user(newUserValue) {
      if (newUserValue) {
        const cartResponse = await axios.get(
          `/api/users/${newUserValue.uid}/cart`,
        );
        const cartItems = cartResponse.data;
        this.cartItems = cartItems;
      }
    },
  },
  methods: {
    async addToCart() {
      await axios.post(`/api/users/${this.user.uid}/cart`, {
        id: this.$route.params.productId,
      });
      alert('Successfully added item to the cart');
    },
    async signIn() {
      const email = prompt('Please enter your email');
      const auth = getAuth();
      const actionCodeSettings = {
        url: `https://linkedin-vue-full-stack.onrender.com/products/${this.$route.params.productId}`,
        handleCodeInApp: true,
      };
      await sendSignInLinkToEmail(auth, email, actionCodeSettings);
      alert('A login link was sent to the email you provided');
      window.localStorage.setItem('emailForSignIn', email);
    },
  },
};
</script>

