<template>
  <h1>Products</h1>
  <ProductListCom :products="products" />
</template>

<script>
import ProductListCom from '@/components/ProductListCom.vue';
import axios from 'axios';

export default {
  name: 'ProductsPage',
  components: { ProductListCom },
  data() {
    return {
      products: [],
    };
  },
  async created() {
    const response = await axios.get('/api/products');
    const products = response.data;
    this.products = products;
  },
};
</script>

